import React, {PureComponent} from 'react';
import Label from 'r-cmui/components/Label';
import Table from 'r-cmui/components/Table';

import Demo1 from './demo1';
import Demo2 from './demo2';
import Demo3 from './demo3';
import Demo4 from './demo4';
import Demo5 from './demo5';
import Demo6 from './demo6';
import Demo7 from './demo7';

class Page extends PureComponent {
    render () {
        return (
            <div className='main-container'>
                <h1 className='page-h1'>CheckBox </h1>
                <blockquote className='page-tip'>
                    
                </blockquote>

                <h1 className='page-h1'></h1>

                <Label grid={0.5} className='code-col'>
                    <Label className='code-box'>
                        <Demo1></Demo1>
                    </Label>
                    <Label className='code-box'>
                        <Demo3></Demo3>
                    </Label>
                    <Label className='code-box'>
                        <Demo5></Demo5>
                    </Label>
                    <Label className='code-box'>
                        <Demo7/>
                    </Label>
                </Label>
                <Label grid={0.5} className='code-col'>
                    <Label className='code-box'>
                        <Demo2></Demo2>
                    </Label>
                    <Label className='code-box'>
                        <Demo4></Demo4>
                    </Label>
                    <Label className='code-box'>
                        <Demo6></Demo6>
                    </Label>
                </Label>

                <h2 className='page-h2'>API</h2>
                <h3 className='page-h3'>CheckBox</h3>

                <Table columns={[
                    {name: 'param', text: ''},
                    {name: 'desc', text: ''},
                    {name: 'type', text: ''},
                    {name: 'default', text: ''}
                ]} bordered={true} data={[
                    {param: 'value', desc: '', type: 'string', default: ''},
                    {param: 'checked', desc: '', type: 'boolean', default: 'false'},
                    {param: 'disabled', desc: '', type: 'boolean', default: 'false'},
                    {param: 'className', desc: 'class', type: 'string', default: ''},
                    {param: 'style', desc: '', type: 'object', default: ''},
                    {param: 'type', desc: 'radiocheckbox', type: 'string', default: 'checkbox'},
                    {param: 'name', desc: 'name', type: 'string', default: ''},
                    {param: 'label', desc: 'label', type: 'string', default: ''},
                    {param: 'onChange', desc: '', type: 'function', default: ''}
                ]}></Table>

                <h3 className='page-h3'>CheckBoxGroup</h3>

                <Table columns={[
                    {name: 'param', text: ''},
                    {name: 'desc', text: ''},
                    {name: 'type', text: ''},
                    {name: 'default', text: ''}
                ]} bordered={true} data={[
                    {param: 'value', desc: '', type: 'string', default: ''},
                    {param: 'data', desc: 'checkbox', type: 'boolean', default: 'false'},
                    {param: 'disabled', desc: '', type: 'boolean', default: 'false'},
                    {param: 'className', desc: 'class', type: 'string', default: ''},
                    {param: 'style', desc: '', type: 'object', default: ''},
                    {param: 'layout', desc: 'inlinestack', type: 'string', default: 'inline'},
                    {param: 'url', desc: '', type: 'string', default: ''},
                    {param: 'name', desc: 'name', type: 'string', default: ''},
                    {param: 'valueField', desc: 'valuedatavalue', type: 'string', default: ''},
                    {param: 'textField', desc: 'datakey', type: 'string', default: ''},
                    {param: 'onChange', desc: '', type: 'function', default: ''}
                ]}></Table>

                <h3 className='page-h3'>Methods</h3>

                <ul className='code-methods'>
                    <li>
                        <i>getValue()</i>
                        <span></span>
                        <ul>
                            <li>
                                return {'String'} 
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>setValue(value)</i>
                        <span></span>
                        <ul>
                            <li>
                                value {'String'} 
                            </li>
                        </ul>
                    </li>
                </ul>
            </div>
        );
    }
}

export default Page;



